/* follow me template */
.made-with-love 
{
    margin-top: 40px;
    padding: 10px;
    clear: left;
    text-align: center;
    font-size: 10px;
    font-family: arial;
    color: #fff;
}
.made-with-love i 
{
    font-style: normal;
    color: #F50057;
    font-size: 14px;
    position: relative;
    top: 2px;
}
.made-with-love a 
{
    color: #fff;
    text-decoration: none;
}
.made-with-love a:hover 
{
    text-decoration: underline;
}


/* for custom scrollbar for webkit browser*/
::-webkit-scrollbar {
    width: 6px;
} 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
} 
::-webkit-scrollbar-thumb {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
}

/* increase, decrease or new */
.inc {
    background-color: #76D376;
}
.dec {
    background-color: #FF6F60;
}
.new {
    background-color: white;
}
